
.history a {
  display: block;
}
.history li {
  width: 100%;
  padding: 8px 0;
  overflow: hidden;
  border-bottom: 1px solid #ccc;
}
.history li .title {
  display: inline-block;
  margin: 10px 0 0 10px;
  width: auto;
  max-width: 80%;
  font-size: 16px;
  line-height: 1.5;
}
.history li img {
  float: right;
  margin-right: 10px;
  width: 70px;
  height: 70px;
}
@media (max-width: 500px) {
.home li .title {
    font-size: 13px;
}
.home button {
    margin: 10px auto;
    height: 50px;
    width: 50px;
}
.home button span {
    display: none;
}
.home button::before,
  .home button::after {
    height: 50px;
    width: 50px;
    border-width: 2px;
}
.home button::after {
    border-width: 2px;
}
}
@-moz-keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@-webkit-keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@-o-keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
@keyframes MoreLoadingAnimation {
0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
}
}


